import Fuse from "fuse.js";
import { createContext } from "react";
// @ts-expect-error DATA_URI gets replaced by Webpack
export const ELEMENT_DATA = fetch(`${DATA_URI}/theseus.json`)
    .then((response) => response.json())
    .then((data) => {
    return {
        elements: data.elements,
        elementMap: new Map(data.elements.map((element) => [element.symbol, element])),
        literateElementMap: new Map(data.elements.flatMap((element) => element.keywords.map((keyword) => [keyword, element]))),
        modifiers: data.modifiers,
        modifierMap: new Map(data.modifiers.map((modifier) => [modifier.symbol, modifier])),
        literateModifierMap: new Map(data.modifiers.flatMap((modifier) => modifier.keywords.map((keyword) => [keyword, modifier]))),
        syntax: data.syntax,
        sugars: new Map(Object.entries(data.sugars)),
        codepage: new Set([...data.codepage, " ", "\n"]),
        codepageRaw: [...data.codepage],
        flagDefs: new Map(data.flags.map((def) => def.type == "choice" ? { ...def, choices: new Map(Object.entries(def.choices)) } : def).map((def) => [def.name, def])),
        version: data.version,
    };
});
export const ElementDataContext = createContext(undefined);
export const elementFuse = new Fuse([], {
    includeScore: true,
    threshold: 0.3,
    isCaseSensitive: true,
    keys: [
        {
            "name": "symbol",
            "weight": 3,
        },
        {
            "name": "name",
            "weight": 2,
        },
        {
            "name": "keywords",
            "weight": 1,
        },
    ],
});
export const modifierFuse = new Fuse([], {
    includeScore: true,
    threshold: 0.3,
    isCaseSensitive: true,
    keys: [
        {
            "name": "name",
            "weight": 2,
        },
        {
            "name": "keywords",
            "weight": 1,
        },
    ],
});
export const syntaxFuse = new Fuse([], {
    includeScore: true,
    threshold: 0.3,
    isCaseSensitive: true,
    keys: [
        {
            "name": "symbol",
            "weight": 3,
        },
        {
            "name": "name",
            "weight": 2,
        },
    ],
});
ELEMENT_DATA.then((data) => {
    elementFuse.setCollection(data.elements);
    modifierFuse.setCollection(data.modifiers);
    syntaxFuse.setCollection(data.syntax);
});
